"use client"
import { useState } from "react"

export default function ChoosePicture({ product }) {
    const [mainpic, setMainpic] = useState(product.pictures[0])
    return (
        <div className="md:flex flex-row md:w-1/2 w-screen gap-5 p-5">
            <div className="md:order-2 order-1">
                <img src={mainpic} alt="mainpic" className="w-full md:w-[295px]" />
            </div>
            <div
                className="cursor-pointer flex md:flex-col flex-row md:order-1 order-2 gap-3">
                {product.pictures.map((url) => (
                    <img
                        onClick={() => setMainpic(url)}
                        className="md:w-[152px] w-[100px]"
                        src={url} key={url} />)
                )}
            </div>
        </div>
    )
}